import { ReactNode } from "react"
import { WatchlistBatchSuggestionPostResponseExtended } from "@rp/react/tanstack-api/suggestion"
import { PrimaryCheckedLabel } from "@rp/react/web/common/base-components"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { Box, Typography } from "@rp/react/web/mui/core"
import { CopyAllOutlined, InfoOutlined } from "@rp/react/web/mui/icons"
import { WatchlistUploadRowActions } from "./WatchlistUploadRowActions"

export type RowItem = {
  data: WatchlistBatchSuggestionPostResponseExtended["entities"][0] & {
    isInWatchlist: boolean
  }
  column: WatchlistUploadColumn
}

export type RowParams = RowItem & {
  onEdit: (item: RowItem) => void
  onDelete: (item: RowItem) => void
  excludeIds?: Array<string>
}

type WatchlistUploadColumn = {
  label: string
  value: string
  width: string
  sortable: boolean
  render: (opts: RowParams) => ReactNode
}

export const WatchlistUploadColumns: Array<WatchlistUploadColumn> = [
  {
    label: "NAME",
    value: "label",
    width: "130px",
    sortable: true,
    render: ({ data }: RowItem) => (
      <Box>
        <Box sx={{ display: "flex", maxWidth: 119 }}>
          <EllipsisTooltip
            tooltipText={data.label}
            typographyProps={{ color: "grey.300", fontSize: 14 }}
          >
            {data.isConflict ? (
              <InfoOutlined sx={{ fontSize: 14, mr: 0.5, pt: "2px", color: "error.light" }} />
            ) : undefined}
            {data.label}
          </EllipsisTooltip>
        </Box>
        {data.isDuplicate && !data.data && (
          <Typography
            sx={{
              color: "primary.main",
              fontSize: 12,
              backgroundColor: "primary.background",
              width: "fit-content",
              px: "6px",
              py: "2px",
              borderRadius: "4px",
              mt: 0.5,
              display: "flex",
              alignItems: "center",
              gap: 0.5,
            }}
          >
            <CopyAllOutlined sx={{ font: "inherit" }} />
            Duplicate
          </Typography>
        )}
      </Box>
    ),
  },
  {
    label: "ENTITY",
    value: "name",
    width: "300px",
    sortable: true,
    render: ({ data }: RowItem) => (
      <Box sx={{ maxWidth: 268 }}>
        <Box sx={{ display: "flex", gap: 0.5, justifyContent: "flex-start", alignItems: "center" }}>
          <EllipsisTooltip tooltipText={data.data?.name ?? ""} typographyProps={{ fontSize: 16 }}>
            {data.data?.name ?? ""}
          </EllipsisTooltip>
          <Typography sx={{ color: "primary.light", fontSize: 12 }}>
            {data.data?.ticker ?? ""}
          </Typography>
        </Box>
        <Box sx={{ display: "flex", gap: 0.5, justifyContent: "flex-start" }}>
          <EllipsisTooltip
            tooltipText={data.data?.description ?? ""}
            typographyProps={{ color: "grey.300", fontSize: 14 }}
          >
            {data.data?.description ?? ""}
          </EllipsisTooltip>
        </Box>
        {data.isInWatchlist && (
          <PrimaryCheckedLabel label="Already in your Watchlist" sx={{ mt: 0.5 }} />
        )}
      </Box>
    ),
  },
  {
    label: "TYPE",
    value: "group1",
    width: "130px",
    sortable: true,
    render: ({ data }: RowItem) => (
      <Box sx={{ display: "flex", maxWidth: 119 }}>
        <EllipsisTooltip
          tooltipText={data.data?.group1 ?? ""}
          typographyProps={{ color: "grey.300", fontSize: 14 }}
        >
          {data.data?.category === "Companies" ? data.data?.group1 : ""}
        </EllipsisTooltip>
      </Box>
    ),
  },
  {
    label: "COUNTRY",
    value: "group2",
    width: "130px",
    sortable: true,
    render: ({ data }: RowItem) => (
      <Box sx={{ display: "flex", maxWidth: 119 }}>
        <EllipsisTooltip
          tooltipText={data.data?.group2 ?? ""}
          typographyProps={{ color: "grey.300", fontSize: 14 }}
        >
          {data.data?.group2}
        </EllipsisTooltip>
      </Box>
    ),
  },
  {
    label: "SECTOR",
    value: "group3",
    width: "130px",
    sortable: true,
    render: ({ data }: RowItem) => (
      <Box sx={{ display: "flex", maxWidth: 119 }}>
        <EllipsisTooltip
          tooltipText={data.data?.group3 ?? ""}
          typographyProps={{ color: "grey.300", fontSize: 14 }}
        >
          {data.data?.group3}
        </EllipsisTooltip>
      </Box>
    ),
  },
  {
    label: "",
    value: "actions",
    width: `${912 - 300 - 130 * 4}px`,
    sortable: false,
    render: (opts: RowParams) => <WatchlistUploadRowActions {...opts} />,
  },
]
